<script>
	import Header from './components/Header.svelte';
    import Footer from './components/Footer.svelte';
    import Filosofia from './components/Filosofia.svelte';
	import Libros from './components/Libros.svelte';
	import Subscribe from './components/Subscribe.svelte';
	import Sugerencias from './components/Sugerencias.svelte';
	import Tabs from './shared/Tabs.svelte'; 
    import Card from './shared/Card.svelte';
	import Button from './shared/Button.svelte';

	let menus = ['FILOSOFIA', 'LIBROS', 'SUSCRIBETE', 'SUGERENCIAS'];
	let menuActivo = 'FILOSOFIA';
	let cookieMessage = true;

	const tabChange = (e) => {
		menuActivo = e.detail;
	};
 
	const handleAdd = (e) => {
		menuActivo = 'FILOSOFIA';
	};

	function closeCookieMessage () {
		cookieMessage = false;
	}
</script>

<Header />

<body>
	<div class="container">
	<div class="left">
	</div>
	
	<div class="center">
		<main> 
			{#if cookieMessage === true}
			<div>
				
				<section class="b2">
				
					Esta web NO utiliza sucias cookies. Tus datos son tuyos
				<button class="b3" on:click={closeCookieMessage}>Cerrar</button>
				</section> 
				
			</div>
			{/if}
			<Tabs {menuActivo} {menus} on:tabChange={tabChange} />
			{#if menuActivo === 'FILOSOFIA'}
			<Filosofia on:add={handleAdd} />
			{/if}
			{#if menuActivo === 'LIBROS'}
				<Libros on:add={handleAdd} />
			{/if}
			{#if menuActivo === 'SUSCRIBETE'}
				<Subscribe on:add={handleAdd} />
			{/if}
			{#if menuActivo === 'SUGERENCIAS'}
			    <Sugerencias on:add={handleAdd} />
		{/if}
		</main>

		<Footer />
	</div>
	
	<div class="right">
	</div>
	</div>
</body>

<style>
	@import url('https://fonts.googleapis.com/css2?family=Boogaloo&display=swap');

	* {
      box-sizing: border-box;
   }
   .left, .right, .center {
      float: left;
      padding: 10px; 
