<script>  
    import { createEventDispatcher } from 'svelte';
    import Button from "../shared/Button.svelte";
    let fields = { propuesta: ''};
    let errors = { propuesta: ''};
    let valid = false;
    let dispatch = createEventDispatcher();
    const suggestURL = 'http://localhost:3000/suggest/';

    const verificaFormatoPropuesta = () => {
        valid = true;

        if (fields.propuesta.trim().length < 1) {
            valid = false;
            errors.propuesta ='Escriba su propuesta';
        } else if (fields.propuesta.trim().length > 15) {
            valid = false;
            errors.propuesta ='Máximo 20 caracteres';
        } else {
            errors.propuesta ='';
        }
    } 

    function enviar () {
        if (valid) {
            console.log('Enviando')
    
            var data = new FormData();
            data.append('propuesta',fields.propuesta);
    
            var requestOptions = {
                method: 'POST',
                body: data,
            };
            fetch(suggestURL,requestOptions)
            .then(res=>window.alert('Gracias!'))
            .catch(res=>window.alert('Algo salio mal'))
            ;
        } else {
            errors.propuesta = 'Formato incorrecto'
        }
    }
</script>

<form on:submit|preventDefault={enviar}>
    <div class="form-field">
        <label for="propuesta"><section>Sugiere el género, sello o artista que te gustaría que 
                   protagonizara alguna referencia de MR White Label:</section></label>
                   <input on:keyup={verificaFormatoPropuesta} type="text" id="email" bind:value="{fields.propuesta}">
        <div class="error">{ errors.propuesta }</div>
    </div>

    <Button type="secondary" flat={true} >Enviar</Button>
    
</form>
    <p>(Algunas ideas: Carl Craig, Drexciya, Electroclash, Ellen Allien, François Kevorkian, 
        Giorgio Moroder, Helena Hauff, Dj Hell, International Deejay Gigolo Records, 
        Ivan Smagghe, Jeff Mills, Masters At Work, Robert Hood, Soma Records, Sven Väth, 
        TR-808, TR-909, etc...)</p>

