<script>  
    import { createEventDispatcher } from 'svelte';
    import Button from "../shared/Button.svelte";
    let fields = { email: ''};
    let errors = { email: ''};
    let valid = false;
    let dispatch = createEventDispatcher();
    const subscribeURL = 'http://localhost:3000/subscribe/';

   const verificaFormatoEmail = () => {
       var regexp = new RegExp(/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/)
        if ( ! regexp.test(fields.email)) {
            valid = false;
            errors.email ='introduzca un formato correcto';
        } else {
            errors.email ='';
            valid = true;
        }
    }  
    function enviar () {
        if (valid) {
            console.log('Enviando')
    
            var data = new FormData();
            data.append('email',fields.email);
    
            var requestOptions = {
                method: 'POST',
                body: data,
            };
            fetch(subscribeURL,requestOptions)
            .then(res=>window.alert('Gracias!'))
            .catch(res=>window.alert('Algo salio mal'))
            ;
        } else {
            errors.email = 'Formato incorrecto'
        }
    }
</script>

<form on:submit|preventDefault={enviar}>
    <section>Si deseas ser el primero en conocer la publicación del próximo
             MR White Label, indica tu email y recibirás el aviso:</section>
    <div class="form-field">
        <label for="email"></label>
        <input on:keyup={verificaFormatoEmail} type="text" placeholder="email" id="email" bind:value="{fields.email}">
        <div class="error">{ errors.email }</div>
    </div>

    <Button type="secondary" flat={true} >Enviar</Button>
</form>

<style>
    form{
        width: 400px;
        margin: 0 auto;
        text-align: center;        
    }
    .form-field{
        margin: 18px auto;     
    }

    input{
        width:100%;
        border-radius:6px;   
    }
    label{
        margin: 10px auto;
        text-align:left;
    }
    .error{
        font-weight: bold;
        font-size: 18px;
        color: #d91b42;
    }
</style>